import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import roomType from 'components/__types__/room';

const RoomFieldTable = ({ t, room }) => (
  <Table>
    <TableHead>
      <TableRow>
        <TableCell>{t('common.name')}</TableCell>
        <TableCell>{t('common.value')}</TableCell>
      </TableRow>
    </TableHead>
    <TableBody>
      <TableRow>
        <TableCell>
          <span>{t('entities.room.id')}</span>
        </TableCell>
        <TableCell data-testid="roomIdValue">
          <span>{room.id}</span>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell>
          <span>{t('entities.room.name')}</span>
        </TableCell>
        <TableCell>
          <span>{room.name}</span>
        </TableCell>
      </TableRow>
    </TableBody>
  </Table>
);

RoomFieldTable.propTypes = {
  room: roomType,
  t: PropTypes.func.isRequired,
};

RoomFieldTable.defaultProps = {
  room: [],
};

export default withTranslation()(RoomFieldTable);
