import React from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import Box from '@material-ui/core/Box';

import roomType from 'components/__types__/room';
import RoomFieldTable from 'components/room-field-table/RoomFieldTable';

const RoomDetails = ({ t, room }) => {
  return (
    <Box>
      <h3 data-testid="details_title">
        {t('common.widgetName', {
          widgetNamePlaceholder: 'Room',
        })}
      </h3>
      <RoomFieldTable room={room} />
    </Box>
  );
};

RoomDetails.propTypes = {
  room: roomType,
  t: PropTypes.func.isRequired,
};

RoomDetails.defaultProps = {
  room: {},
};

export default withTranslation()(RoomDetails);
